<template>
  <div class="text-white">
    <div class="my-2 text-center">
      <a
        href="https://youtube.com/playlist?list=PLqyVsQxV6gqfpfLl_golBk44xx3TopB09"
        target="_blank"
        class="
          inline-block
          px-3
          py-2
          mt-3
          text-center text-white
          transition
          bg-green-500
          rounded
          hover:bg-green-600
        "
      >
        Tutorial</a
      >
    </div>
    <div
      class="
        container
        p-5
        mx-auto
        mt-5
        space-y-3
        text-white
        bg-gray-800
        rounded
        text-ceter
      "
    >
      <template v-for="(p, i) in userData" :key="i">
        <p class="text-lg" v-if="i && p && i !== 'pass' && i !== 'cpass'">
          <span class="capitalize">{{ i }}</span> : {{ p }}
        </p>
      </template>
    </div>
    <!-- <div class="text-center">
      <button
        class="
          inline-block
          px-3
          py-2
          mt-3
          text-center text-white
          transition
          bg-red-500
          rounded
          hover:bg-red-600
        "
        @click="signOut"
      >
        Logout
      </button>
    </div> -->
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      userData: {},
    };
  },
  methods: {
    async getUserData() {
      let res = await fetch(
        `https://script.google.com/macros/s/AKfycbzFgqSH8kZIIIKtH6C7P7TzEMfJgXoiNP2VdCZf7n_hLxyq3OLFMEUtuBSgIY3agt7i/exec?sheet=${this.user.displayName}&type=Initial Data`
      );
      let data = await res.json();

      this.userData = data.routines[0];
    },
    ...mapActions(["signOut"]),
  },
  created() {
    this.getUserData();
  },
  computed: {
    ...mapState(["user"]),
  },
};
</script>

